import React, { useState, useEffect }  from "react";

/* ***********************
**   Import Material-UI
*********************** */
import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid'

import Web3 from "web3";

/* ***********************
**   Import images
*********************** */
import logo from '../../../src/img/logo.png'


var web3;

var accounts = [];
let finalAccount;

const Menu = () =>{
    const [useWallet,setWallet] = useState('Connect');

    /***************************************************
     *
     *    Slicing functions
     *
     ****************************************************/
  
    const  connectWallat = () =>{
        //if metamask is installed
        if(window.web3 !== undefined){
            //connect to web3
            web3 = new Web3(window.ethereum);
            // gasPrice's contract's credentials

            window.ethereum.request({ method: 'eth_requestAccounts' }).then(_accounts => {
                console.log("fectum Wallet",_accounts);
                accounts = _accounts;
                finalAccount = accounts.toString();

                const start_char = finalAccount.toString().substring(0,5)+".....";
                const end_char = finalAccount.toString().substr(finalAccount.length - 4);
                setWallet(start_char+end_char)
                // finalAccount = '0x716e43726C5212404D011a256ba1Ea5c0965bf87';
                console.log("new acc",finalAccount);
            });  
        }
        else{
            //if Metamask is not installed
        }
    }
    useEffect(()=> {
       
        connectWallat();
    },[accounts,finalAccount]);



    return(
        <div className="SCB---menu">
            <Container maxWidth="lg">
                <Grid
                    container
                    direction="row"
                    justify="flex-start"
                    alignItems="center"
                    xs={12}
                >
                    <Grid
                        xs={6}
                        sm={6}
                        style={{textAlign:'left'}}
                    >
                        <a href="/">
                            <img src={logo} style={{height:90, width: 'auto'}} />
                        </a>
                    </Grid>
                    <Grid
                        xs={6}
                        sm={6}
                        style={{textAlign:'right'}}
                    >
                        {/* <button href='#' onClick={connectWallat} title={useWallet} >{ useWallet }</button> */}
                        <button className="btn">BUY $SCB</button>
                        <button className="btn SCB-d-none-sm" onClick={connectWallat}>{useWallet}</button>
                    </Grid>

                    <Grid
                        xs={12}
                        style={{textAlign:'right'}}
                    >
                        <button className="btn SCB-d-none-md" onClick={connectWallat}>{useWallet}</button>
                    </Grid>
                </Grid>
            </Container>
        </div>
    );
}
export default Menu;